import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";

import { DashboardComponent } from "./dashboard/dashboard.component";

const routes: Routes = [
   {
      path: "",
      redirectTo: "dashboard"
   }, {
      path: "dashboard",
      component: DashboardComponent
   }, {
      path: "settlement",
      loadChildren: () => import("./settlement/settlement.module").then((m) => m.SettlementModule),
   }, {
      path: 'non-financial-alteration',
      loadChildren: () => import('./non-financial-alteration/non-financial-alteration.module').then(m => m.NonFinancialAlterationModule)
   },
];

@NgModule({
   imports: [RouterModule.forChild(routes)],
   exports: [RouterModule],
})
export class PagesRoutingModule { }
